import React from 'react';
import {makeStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Container from "@material-ui/core/Container";
import TextField from "@material-ui/core/TextField";

const useStyles = makeStyles(theme => ({
  root: {
    '& > *': {
      margin: theme.spacing(1),
      width: '25ch',
    },
  },
}));

function Contacts(){
    const classes = useStyles();
    return(
        <div className='contact-us-6'>
          <Container maxWidth='lg'>
            <h1>CONTACT US</h1>
            <div className='contact-us-container'>
              <form className={classes.root} noValidate autoComplete="off">
                <TextField id="standard-basic" label="Name" name='name' />
                <TextField id="filled-basic" label="Email" name='email'  />
                <TextField id="outlined-basic" label="Subject" name='subject' />
                <TextField
                  id="outlined-multiline-static"
                  label="Message"
                  name='message'
                  multiline
                  rows="4"
                  variant="outlined"
                />
                <Button className={'small'} variant="contained" >Send</Button>
              </form>
              <div className='google-map'>
                <img src="images/map.png" alt=""/>
              </div>
            </div>
          </Container>
        </div>
    )
}


export default Contacts;
